<template>
  <div class="login">
    <div class="title">简易聊天室</div>
    <input class="name-input" type="text" v-model="userName" placeholder="请输入你的名字" />
    <div v-if="errTips">{{errTips}}</div>
    <button class="login-btn" @click="handleLogin">登录</button>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      userName: "",
      errTips: ""
    };
  },
  mounted() {
    this.sockets.subscribe("relogin", data => {
      console.log(data);
    });
  },
  methods: {
    async handleLogin() {
      this.errTips = "";
      if (!this.userName) {
        this.errTips = "请输出你的名字^_^";
        return;
      }
      this.$socket.emit("login", {
        username: "username",
        password: "password"
      });
    }
  }
};
</script>
